import { AuthService } from "./../../login/services/auth.service";
import { Component, OnInit, ElementRef, Output } from "@angular/core";
import { AppConfig } from "../../app.config";
import { Router } from "@angular/router";
import { saveAs } from "file-saver";
import { environment } from "./../../../environments/environment";

import { Observable, of } from "rxjs";
import { HttpClient} from "@angular/common/http";

declare let jQuery: any;

@Component({
	selector: "app-navbar",
	styleUrls: ["./navbar.style.scss"],
	templateUrl: "./navbar.template.html"
})
export class NavbarComponent implements OnInit {
	$el: any;
	config: any;
	private mgturl = environment.apiurl + "/api/v1/mgmt";
	private deadlinesFileName: string="Transfer Deadlines.xlsx";
	private transferStatusFileName: string="Transfer Status.xlsx"

	constructor(
		el: ElementRef,
		private router: Router,
		config: AppConfig,
		private auth: AuthService,
		private http: HttpClient
	) {
		this.$el = jQuery(el.nativeElement);
		this.config = config.getConfig();
	}

	ngOnInit(): void {
		jQuery(".navbar .nav-item .nav-link").on("click", function() {
			jQuery(".navbar")
				.find(".active")
				.removeClass("active");
			jQuery(this).addClass("active");
		});
	}

	logout() {
		// this.auth.amp_signOut();
		this.router.navigate(["/login"]);
	}

	setNavItemActive(elemID: any) {
		console.log(elemID);
	}

	public downloadDeadlines(){
	
		this.exportDeadlineData().subscribe(res=>{
				saveAs(res,this.deadlinesFileName);
			});

	}

	public downloadTransferStatus(){
		this.exportTransferStatusData().subscribe(res=>{
				saveAs(res, this.transferStatusFileName);
			});

	}

	private exportDeadlineData():Observable<Blob> {
		return 	this.http
		.get(this.mgturl + "/view/transferdeadlines", { responseType: 'blob' })
		.pipe(res=>res);
	}

	private exportTransferStatusData():Observable<Blob> {

		return 	this.http
		.get(this.mgturl + "/view/transferstatuses",{responseType: 'blob'})
		.pipe(res=>res);
	}
}
