import { Component, ViewEncapsulation } from '@angular/core';
import { Observable, timer } from 'rxjs';
import { AuthService } from '../login/services/auth.service';
import { Subscription } from 'rxjs';
import { Router, ActivatedRoute, NavigationExtras, NavigationStart, NavigationEnd } from '@angular/router';
import { filter } from 'rxjs/operators';
import { isNil } from "ramda";
import { NGXLogger } from 'ngx-logger';
declare var jQuery: any;

@Component({
    selector: '[session-expired-modal]',
    templateUrl: './session-expired.modal.html',
    encapsulation: ViewEncapsulation.None
})

export class SessionExpiredComponent {

    public timeToExpire: string;
    public timeToExpireUOM: string;

    private countDownSeqGenerator: Observable<any>;
    private period: number = 10;
    private timeBeforeExp: number;
    private timeToCntDwnSeq: number;

    private keepSignedIn: boolean = false;

    private sessionExpSubscription: Subscription;
    private routerSubscription: Subscription;

    private subscriptions: Subscription[] = [];

    private UOM_MINUTE = "minutes";
    private UOM_SECOND = "seconds";

    constructor(
        private authService: AuthService,
        private router: Router,
        private logger: NGXLogger
    ) {
        this.router.events.pipe(
            filter(event => event instanceof NavigationEnd)
        ).subscribe((e: NavigationEnd) => {
            if (e.url.includes("login"))
                return;

            if (this.authService.jwt_loggedIn())
                this.refreshSession();

            this.logger.trace("Navigating to ", e.url);
        });

        this.timeBeforeExp = this.authService.jwt_getTokenExpWarnTime();
        console.log(this.timeBeforeExp);
        
        this.createSessionExpirationTimer();
    }

    ngOnInit() { }

    ngOnDestroy() {
        for (let i in this.subscriptions) {
            this.subscriptions[i].unsubscribe();
        }

        if (this.routerSubscription)
            this.routerSubscription.unsubscribe();
    }


    showModal() {
        let modal = jQuery("#session-expired-modal");
        if (!modal.is(':visible')) {
            modal.modal({
                toggle: 'show',
                backdrop: 'static',
                keyboard: false
            })
        }
    }

    public hideModal() {
        let modal = jQuery("#session-expired-modal");
        if (modal.is(':visible'))
            modal.modal("hide");
    }

    public keepSessionAlive() {
        this.keepSignedIn = true;
        this.refreshSession();
    }

    public refreshSession() {
        this.cleanupSessionExpiration();
        let that = this;
        this.authService.jwt_refreshToken().subscribe(
            data => {
                let body: any = data;
                if (!isNil(body.token)) {
                    that.authService.jwt_setAccessToken(body.token);
                    that.createSessionExpirationTimer();
                    that.keepSignedIn = false;
                } else {
                    that.authService.jwt_removeAccessToken();
                    that.router.navigate(['./login']);
                }
            }, error => {
                this.logger.error("session-expired.component", "Error Refreshing Session Token");
                that.authService.jwt_removeAccessToken();
                that.router.navigate(['./login']);
            });
    }

    logout() {
        this.cleanupSessionExpiration();
        this.authService.jwt_logout();
    }

    cleanupSessionExpiration() {
        this.logger.trace("session-expired.component", "CleanupSessionExpiration called - should hide modal window");
        this.hideModal();
        for (let i in this.subscriptions) {
            this.subscriptions[i].unsubscribe();
        }
    }

    private createSessionExpirationTimer() {
        this.logger.trace("session-expired.component", "createSessionExpirationTimer called");
        let that = this;
        let expirationTime = this.authService.jwt_getTokenExpirationTime();
        this.timeToCntDwnSeq = expirationTime - this.timeBeforeExp;
        this.countDownSeqGenerator = timer(this.timeToCntDwnSeq, this.period);

        this.sessionExpSubscription = this.countDownSeqGenerator.subscribe(
            cnt => {
                this.logger.trace("session-expired.component", "countDownSeqGenerator subscription handled");
                let timeToExp: number = (that.timeBeforeExp / 1000) - cnt;
                if ((timeToExp / 60) > 1) {
                    let secondsPart: string = String(timeToExp % 60);
                    if (secondsPart.length === 1) secondsPart = "0" + secondsPart;
                    that.timeToExpire = Math.floor(timeToExp / 60) + ":" + secondsPart;
                    that.timeToExpireUOM = that.UOM_MINUTE;
                } else if (timeToExp >= 0) {
                    that.timeToExpire = timeToExp.toString();
                    that.timeToExpireUOM = that.UOM_SECOND;
                }

                if (timeToExp < 0) {
                    this.logger.trace("session-expired.component", "Time out exceeded: timeexp is < 0; Logging out automatically");
                    that.cleanupSessionExpiration();
                    that.logout();
                } else {
                    if (that.keepSignedIn === true) {
                        this.logger.trace("session-expired.component", "insde counter timer:keepsignedtrue calling cleanupsession");
                        that.cleanupSessionExpiration();
                    } else {
                        this.logger.trace("session-expired.component", "Show Session Expiration Window called");
                        that.showModal();
                    }
                }
            }
        );

        this.logger.trace("session-expired.component", "subscription counter is", this.subscriptions.length.toString());
        this.subscriptions.push(this.sessionExpSubscription);
    }

}